export const getAlleviaDuoProductCode = (state) => {
    return state.alleviaDuo
        ? state.pumpOption === "withPump"
            ? "AIRFOAP-0-AF2-000"
            : state.pumpOption === "withoutPump"
                ? "AIRFOA-0-AF2-000"
                : null
        : null;
};
